@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100;0,200;0,300;1,100;1,200&family=Oswald:wght@200;300;400&display=swap);
* {
  font-family: 'Josefin Sans', sans-serif;
  font-size: medium
}

body { margin: 0 }

* > button , input , select , option {
  background-color: rgb(221, 222, 223);
}

span {
  font-size: 30px;
  margin-left: 5px;
}

.pageLogin {
  align-items: center;
  background: linear-gradient( 90deg,
    #4287f5 41%, #f2f2f2 0%);
  border-radius: 10px;
  box-shadow: 0 5px 5px grey;
  display: flex;
  justify-content: space-around;
  height: 500px;
  margin-left:auto;
  margin-right: auto;
  margin-top: 150px;
  width: 35%;
}

.pageLogin div {
  display: flex;
  flex-direction: column;
  margin-right: 8%;
}

.tittleLogin {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
  font-size: 35px;
}

.emote {
  font-size: 200px;
}

Input, button {  
  border: none;
  border-radius: 5%;
  height: 50px;
  margin-bottom: 10px;
  text-align: center;
  width: 214px;

}

.pageLogin button {
  background-color: #1d72fb;
  color: white;
  width: 218px;
  border: none;
  box-shadow: 0 10px grey;

}

.pageLogin button:disabled {
  box-shadow: none;
}

.pageLogin button:active {
  box-shadow: 0 4px rgb(94, 93, 93);
  transform: translateY(6px);
}

.headerInfo {
  background-color: rgb(60, 143, 221);
  color: white;
  display: flex;
  justify-content: flex-start;
  margin-bottom: 10px;
}

.headerInfo p {
  background-color: rgb(60, 143, 221);
  margin-left: 10px;
  margin-right: 50px;
}

.headerInfo span {
  align-content: center;
  background-color: rgb(60, 143, 221);
  margin-left: 35px;
  margin-top: 5px;
}

.despesas * {
  border: none;
  border-radius: 5%;
}

.despesas {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-bottom: 10px;
}

.despesas input , select {
  margin-left: 5px;
  width: 100px;
}

.despesas button {
  background-color: rgb(60, 143, 221);
  border: solid 0.5px black;
  color: white;
  width: 100px;
}

table {
  margin-left: 38px;
  margin-right: 37px;
}

th {
  background-color: rgb(60, 143, 221);
  color: white;
  padding: 0 30px;
}

th , td , button {
  border-radius: 3px;
  border-spacing: 0;
}

.detalhes td > button {
  background-color: red;
  color: white;
  width: 100%;
}

